.InputSearch{
    margin: 0 24px;
    height: 64px;
    width: calc(100% - 62px);
    margin-bottom: 10px;

    color: black;
    background: #f9fbfc;
    border: 2px solid #fafafa;
    border-radius: 2px;
    
    font-size: 20px;
    font-weight: 300;
    text-align: center;
    
}


.InputSearch::placeholder{
    color: #a5a5a5;
    font-weight: 400;
}

.InputSearch:focus{
    outline: 4px solid;
    outline-color: #ff595e;
}